//
// Buttons
// --------------------------------------------------


// Override button global settings

.btn {
  letter-spacing: $btn-letter-spacing;
  white-space: nowrap;

  > i { vertical-align: middle; }

  &.active { cursor: default; }
  &.disabled,
  &:disabled {
    opacity: .5;
    cursor: not-allowed;
  }
}


// Overriding button variant colors
//
// Solid variant

@each $color, $value in map-remove($theme-colors, 'light') {
  .btn-#{$color} {
    color: $btn-dark-color;

    &:hover,
    &:focus,
    &:active,
    &.active,
    &.show {
      background-color: darken($value, 7.5%);
      border-color: darken($value, 7.5%);
      color: $btn-dark-color;
    }

    &:disabled,
    &.disabled {
      color: $btn-dark-color;
    }
  }
}

.btn-secondary {
  & { color: $gray-800; }
  &:hover,
  &:focus,
  &:active,
  &.active,
  &.show {
    color: $gray-900;
  }

  &:disabled,
  &.disabled {
    color: $btn-light-color;
  }
}

.btn-light {
  border-color: transparent;
  background-color: $light;
  color: $btn-light-color;

  &:hover,
  &:focus,
  &:active,
  &.active {
    border-color: darken($light, 7.5);
    background-color: darken($light, 7.5);
  }
}


// Outline variant

[class^='btn-outline-'],
[class*=' btn-outline-'] {
  &:hover, &:active,
  &.active, &.dropdown-toggle.show {
    color: $btn-dark-color;
  }
}

@each $color, $value in $border-colors {
  .btn-outline-#{$color} {
    border-color: $value;
  }
}

.btn-outline-secondary {
  border-color: $gray-400;
  color: $btn-light-color;

  &:hover, &:focus, &:active,
  &.active, &.show {
    border-color: $gray-300;
    background-color: $gray-300;
    color: $gray-900 !important;
  }
}

.btn-outline-light {
  border-color: $light;
  &:hover, &:focus, &:active,
  &.active, &.show {
    background-color: $light;
    color: $gray-800;
  }
}


// Translucent variant

[class^='btn-translucent-'],
[class*=' btn-translucent-'] {
  &:hover, &:focus, &:active,
  &.active, &.show {
    color: $btn-dark-color;
  }
}

@each $color, $value in $theme-faded-colors {
  .btn-translucent-#{$color} {
    background-color: $value;
  }
}

@each $color, $value in map-remove($theme-colors, 'secondary', 'light') {
  .btn-translucent-#{$color} {
    color: $value;
    &:hover, &:focus, &:active,
    &.active, &.show {
      background-color: $value;
    }
  }
}

.btn-translucent-light {
  color: $light;

  &:hover,
  &:focus,
  &:active,
  &.active,
  &.show {
    background-color: $light;
    color: $gray-900;
  }
}


// Gradient button

.btn-gradient {
  background-color: $primary;
  background-image: $gradient;

  &,
  &:hover,
  &:focus,
  &:active,
  &.active,
  &.show {
    color: $btn-dark-color;
  }
}


// Link button

.btn-link {
  font-weight: $btn-font-weight;
  text-decoration: $btn-link-decoration;

  &:hover,
  &:focus,
  &:active,
  &.active,
  &.show {
    text-decoration: $btn-link-hover-decoration;
  }
}


// Shadow on hover effect

.btn-hover-shadow:hover {
  @each $color, $value in $theme-colors {
    &.btn-#{$color},
    &.btn-outline-#{$color},
    &.btn-translucent-#{$color} {
      box-shadow: 0 .5rem 1.125rem -.5rem rgba($value, .92);
    }
  }
  &.btn-gradient {
    box-shadow: 0 .5rem 1.125rem -.5rem rgba($gradient-from-color, .92);
  }
}


// Icons
//
// Alignment

.btn {
  > [class^='ci-'],
  > [class*=' ci-'] {
    margin-top: -.125rem;
    vertical-align: middle;
    @include font-size($btn-icon-size);
  }
}

// Icon button

.btn-icon { padding: $btn-padding-y ($btn-padding-y * 1.3); }


// Market buttons

.btn-market {
  display: inline-block;
  padding: $btn-market-padding-y $btn-market-padding-x;
  padding-left: $btn-market-padding-x * 1.5 + $btn-market-icon-size;
  transition: $btn-market-outline-transition;
  @include border-radius($btn-market-border-radius);
  background: {
    position: center left .75rem;
    color: $btn-market-bg;
    size: $btn-market-icon-size $btn-market-icon-size;
    repeat: no-repeat;
  }
  text-decoration: none;
  white-space: nowrap;
  .btn-market-title {
    display: block;
    color: $btn-market-title-color;
    @include font-size($btn-market-title-font-size);
  }
  .btn-market-subtitle {
    display: block;
    margin-bottom: -.25rem;
    color: $btn-market-subtitle-color;
    font-size: $btn-market-subtitle-font-size;
  }
  &:hover {
    background-color: $btn-market-hover-bg;
    text-decoration: none;
  }
}

// App Store
.btn-apple {
  background-image: url(data:image/svg+xml;utf8;base64,);
}

// Google Play
.btn-google {
  background-image: url(data:image/svg+xml;utf8;base64,);
}

// Windows Store
.btn-windows {
  background-image: url(data:image/svg+xml;utf8;base64,);
}

// Amazom Market
.btn-amazon {
  background-image: url(data:image/png;base64,);
}


// Scroll to top button

.btn-scroll-top {
  display: block;
  position: fixed;
  right: $spacer * 1.25;
  bottom: $spacer * 1.25;
  width: $btn-scroll-top-size;
  height: $btn-scroll-top-size;
  transition: $btn-scroll-top-transition;
  transform: scale(0);
  @include border-radius(50%);
  background-color: $btn-scroll-top-bg;
  color: $btn-scroll-top-color;
  text-align: center;
  opacity: 0;
  z-index: $zindex-btn-scroll-top;

  > .btn-scroll-top-icon {
    @include font-size($btn-scroll-top-icon-font-size);
    font-weight: $font-weight-base;
    line-height: $btn-scroll-top-size - .125rem;
  }

  .btn-scroll-top-tooltip {
    position: absolute;
    top: 50%;
    right: 100%;
    transform: translateY(-50%);
    transition: opacity .25s ease-in-out, visibility .25s ease-in-out;
    visibility: hidden;
    opacity: 0;
  }

  &:hover {
    text-decoration: none;
    background-color: $btn-scroll-top-hover-bg;
    color: $btn-scroll-top-hover-color;
    .btn-scroll-top-tooltip {
      visibility: visible;
      opacity: 1;
    }
  }

  &.show {
    opacity: 1;
    transform: scale(1.001);
  }

  &.right-15 {
    right: ($spacer * 1.25) + .9375rem;
  }

  @include media-breakpoint-down(sm) {
    width: $btn-scroll-top-size * .75;
    height: $btn-scroll-top-size * .75;
    right: $spacer;
    bottom: $spacer;
    > .btn-scroll-top-icon {
      line-height: $btn-scroll-top-size * .75;
    }
    &.right-15 {
      right: $spacer + .9375rem;
    }
  }
}


// Close button

.btn-close { transition: $btn-close-transition; }
